import React, { Component } from 'react'

class List extends Component {
    render() {
        let arr = this.props.todos.filter(item=>!item.done)
        return (
            <div>
                <h2>正在进行{arr.length}</h2>
                <ul>
                    {this.props.renderTodos(false)}
                </ul>
                <h2>已经完成</h2>
                <ul>
                    {this.props.renderTodos(true)}
                </ul>
            </div>
        )
    }
}
export default List;
